body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

*{
  list-style: none;
}
* {
  margin: 0;
  padding: 0; }

.searchbar {
  margin: 0 40px;
  margin-top: 10px; }
  .searchbar input {
    width: 100%;
    height: 2em; }

.key-list {
  overflow: hidden;
  margin: 10px 40px; }

.key {
  color: white;
  float: left;
  border-radius: 5px;
  background: #207df8;
  padding: 0 4px;
  margin-right: 2px;
  margin-bottom: 2px;
  cursor: pointer; }

.video-list {
  overflow: auto;
  margin: 40px; }

.title, .video .title {
  white-space: normal;
  line-height: 1.2;
  height: 2.4em;
  text-align: left;
  word-wrap: break-word;
  display: -webkit-box;
  -webkit-line-clamp: 2; }

.video {
  margin-right: 4px;
  display: inline-block;
  width: 235px;
  margin-bottom: 24px;
  overflow: hidden;
  cursor: pointer; }

.video-detail {
  display: inline-block;
  padding-left: 40px;
  width: 560px; }

